@import url(https://fonts.googleapis.com/css2?family=Markazi+Text:wght@400;600;700&display=swap);
.sidebar__wrapper {
    color: var(--orange);
    background: var(--dark);
    box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1), 1px 1px 3px rgba(0, 0, 0, 0.8);
    flex: 0.1 1;
    height: 100vh;
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    top: 0;
}

.sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    font-size: 22px;
    height: 100%;
}

.home {
    flex: 0.3 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar>ul {
    list-style: none;
    display: flex;
    flex: 0.2 1;
    flex-direction: column;
    height: 150px;
    justify-content: space-between;
    margin-bottom: 0px !important;
}

.sidebar__link-active {
    color: var(--blue);

}

i {
    box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1),
        1px 1px 3px rgba(0, 0, 0, 0.8);
    padding: 12px;
    border-radius: 50%;
    margin: 10px 0;
}

.sidebar__social {
    flex: 0.3 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar__social>i {
    border-radius: 10%;
    color: var(--white);
    flex: 0.3 1;
    text-align: center;
}

.fa-rocket {
    color: var(--pink);
}

.fa-home {
    color: var(--violet);
}

.fa-search-location {
    color: var(--green);
}

.fa-github-alt {
    color: var(--white);
}

@media (max-width:1160px) {
    .sidebar__wrapper {
        height: 100px;
        z-index: 1;
    }

    .sidebar {
        flex-direction: row;
        align-items: center;
    }

    .sidebar>ul {
        flex-direction: row;
        height: 85px;
        width: 20%;
        align-items: center;
        flex: 0.6 1;
    }

}
.app {
  display: flex;
}
.landing {
    flex: 1 1;
    padding: 0 2%;
    display: grid;
    place-items: center;
    font-family: 'Markazi Text', serif;
    line-height: 50px;
}

.landing .row {
    align-items: center;
}

.landing__first {
    height: 100vh;
}

.landing__image {
    width: 100%;
}

.landing__left {
    padding: 2%;
}

.landing__right {}

.landing__text {
    font-size: 2.2rem;

}

.landing__text-top {
    font-size: 1.3rem;
    letter-spacing: 0.5px;
    /* color: var(--green); */
    color: #d0b53f;
}

.landing__text-title {
    font-size: 3rem;
    font-weight: 600;
    /* color: var(--pink); */
    color: #d0373a;
}

.landing__buttons {
    display: flex;
    margin-top: 8%;
    text-align: center;
}

.landing a {
    flex: auto;
    margin-right: 2%;
}

.landing button {
    font-size: 1.3rem;
    color: #fff;
    font-weight: 700;
    width: 100%;
    height: 50px;
    border-radius: 30px;
    /* background-image: linear-gradient(to right bottom, var(--pink), var(--violet)); */
    background-image: linear-gradient(to right bottom, #bd2629, #d4803b);
}

.landing__buttons-path {
    background-image: linear-gradient(to right bottom, #d7bc48, #cf6726) !important;
}

.strikeout {
    position: relative;
}

.strikeout::after {
    border-bottom: 0.125em solid var(--violet);
    content: "";
    left: 0;
    margin-top: calc(0.125em / 2 * -1);
    position: absolute;
    right: 0;
    top: 55%;
}

.landing__buttons-portfolio {
    background-image: linear-gradient(to right bottom, var(--pink), var(--violet), var(--green)) !important;
    letter-spacing: 1px;

}

.landing__second-header {
    font-size: 6rem !important;
}
.sorting {
    flex: 1 1;
    padding: 0 2%;
}

.sorting__navbar {
    padding: 10px;
}

.util__buttons {
    margin-bottom: 1%;
}

.sorting-types__buttons {
    margin-bottom: 1%;
}

.sorting-types__buttons>button {
    color: var(--orange);
}

.util__buttons>button {
    color: var(--violet);
}

.sorting__container {
    display: flex;
    padding: 10px;

}

.sorting__array {
    background-color: transparent;
    padding: 20px 0px;
    height: 460px;
    margin: 10px auto;
    text-align: center;
    flex: 0.8 1;
    border-radius: 7px;

}

.array-bar {
    width: 0.8%;
    background-color: var(--orange);
    display: inline-block;
    margin: 45px 2px;
    border-radius: 50px 50px 0px 0px;
}

.sorting__articles {
    flex: 0.2 1;
    border: 1px solid var(--orange);
}

.arraySize-slider {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    height: 7px;
    width: 30%;
    min-width: 300px;
    margin-left: 10px;

    background: var(--orange);
    border-radius: 7px;
    outline: none;
}

.arraySize-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
            appearance: none;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background: var(--orange);
    cursor: pointer;
}

.sorting__button-active {
    border: 3px solid var(--orange)
}
.node {
    width: 30px;
    height: 30px;
    outline: 1px solid var(--green);
    background: var(--black);
    display: inline-block;
}

.node-start {
    position: relative;
}

.node-start:before {
    position: absolute;
    font-family: 'FontAwesome';
    top: 0px;
    left: 3px;
    content: "\f1b9";
    font-size: 20px;
    color: var(--violet);
}

.node-end {
    position: relative;
}

.node-end:before {
    position: absolute;
    font-family: 'FontAwesome';
    top: 0px;
    left: 3px;
    content: "\f11e";
    font-size: 20px;
    color: rgb(218, 39, 8);
    /* color: var(--pink); */
}

.node-wall {
    -webkit-animation-name: wallAnimation;
            animation-name: wallAnimation;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes wallAnimation {
    0% {
        transform: scale(.3);
        background-color: var(--pink);
    }

    50% {
        transform: scale(1.2);
        background-color: var(--pink);
    }

    100% {
        transform: scale(1.0);
        background-color: var(--pink);
    }
}

@keyframes wallAnimation {
    0% {
        transform: scale(.3);
        background-color: var(--pink);
    }

    50% {
        transform: scale(1.2);
        background-color: var(--pink);
    }

    100% {
        transform: scale(1.0);
        background-color: var(--pink);
    }
}

.node-visited {
    -webkit-animation-name: visitedAnimation;
            animation-name: visitedAnimation;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
    0% {
        transform: scale(.1);
        background-color: rgba(2, 252, 98, 0.75);
        border-radius: 100%;
        outline: none;
    }

    40% {
        transform: scale(.6);
        border-radius: 100%;
        outline: none;
        background-color: rgba(1, 247, 124, 0.75);
    }

    85% {
        transform: scale(1.2);
        background-color: rgba(30, 168, 131, 0.75)
    }

    100% {
        transform: scale(1);
        background-color: rgba(3, 80, 73, 0.815);
    }
}

@keyframes visitedAnimation {
    0% {
        transform: scale(.1);
        background-color: rgba(2, 252, 98, 0.75);
        border-radius: 100%;
        outline: none;
    }

    40% {
        transform: scale(.6);
        border-radius: 100%;
        outline: none;
        background-color: rgba(1, 247, 124, 0.75);
    }

    85% {
        transform: scale(1.2);
        background-color: rgba(30, 168, 131, 0.75)
    }

    100% {
        transform: scale(1);
        background-color: rgba(3, 80, 73, 0.815);
    }
}

.node-shortest-path {
    -webkit-animation-name: shortestPath;
            animation-name: shortestPath;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes shortestPath {
    0% {
        transform: scale(0.6);
        border-radius: 100%;
        background-color: rgb(250, 54, 4);
        outline: none;
    }

    50% {
        transform: scale(1.2);
        background-color: rgb(248, 252, 1);
        outline: none;
    }

    100% {
        transform: scale(1);
        background-color: #FFFE6A
    }
}

@keyframes shortestPath {
    0% {
        transform: scale(0.6);
        border-radius: 100%;
        background-color: rgb(250, 54, 4);
        outline: none;
    }

    50% {
        transform: scale(1.2);
        background-color: rgb(248, 252, 1);
        outline: none;
    }

    100% {
        transform: scale(1);
        background-color: #FFFE6A
    }
}
.pathFinder {
    flex: 1 1;
    padding: 0 2%;
}

.pathFinder__container {}

.pathFinder__grid {
    background-color: transparent;
    padding: 20px 0px;
    height: 420px;
    margin: 10px auto;
    text-align: center;
}


.grid {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 20px auto;
}

.grid__row {
    display: flex;
    justify-content: center;
}

.pathFinder__navbar {
    padding: 10px;
    margin: 1% auto;
}

.pathFinder__types>button {
    color: var(--green);
}

.indicators {
    display: flex;
    box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1),
        1px 1px 3px rgba(0, 0, 0, 0.8);
    width: 90%;
    justify-content: space-around;
    margin: 4% auto;
    padding: 1%;
}

.wall__icon {
    height: 30px;
    width: 30px;
    background-color: var(--pink);
}

.path__icon {
    height: 30px;
    width: 30px;
    background-color: rgb(255, 254, 106);
}

.visited__icon {
    height: 30px;
    width: 30px;
    background-color: rgba(3, 80, 73, 0.815);
}

.start>i {
    color: var(--violet);
}

.end>i {
    color: rgb(218, 39, 8);
}

.pathFinder__button-active {
    border: 3px solid var(--green);
}
.articles__wrapper {
    flex: 0.2 1;
    box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1), 1px 1px 3px rgba(0, 0, 0, 0.8);
    padding: 1%;
    height: 100vh;
    position: -webkit-sticky;
    position: sticky;
    right: 0;
    top: 0;
}
.p5page {
    flex: 1 1;
    padding: 0 2%;

}

.todo {
    box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1),
        1px 1px 3px rgba(0, 0, 0, 0.8);
    padding: 2%;
    margin: 2%;
    border-radius: 10px;
}

.todo__title {
    -webkit-text-decoration: wavy;
            text-decoration: wavy;
    color: var(--green);
    margin-bottom: 4%;
}
.feedback__form {
    margin: 4% auto;
    padding: 2%;
    width: 90%;
    text-align: center;
    border-radius: 10px;
    background: #20232F;
    box-shadow: inset 14px 14px 29px #1a1c26,
        inset -14px -14px 29px #262a38;
    display: flex;
    flex-direction: column;
}

.form__dummyDiv {
    display: flex;
    justify-content: space-between;
}

.form__textField {
    background-color: transparent;
    margin-bottom: 2%;
    border-radius: 10px;
    color: var(--green);
    padding: 2%;
    border-color: var(--green);
    outline-color: var(--green);
    border-width: 2px;
}

.form__input {
    background-color: transparent;
    border-radius: 10px;
    color: var(--green);
    padding: 1%;
    border-color: var(--green);
    outline-color: var(--green);
    border-width: 2px;

    width: 30%;
}

.form__input:focus,
.form__textField:focus {
    outline: none;
}

.feedback__form button {
    color: var(--green);
}
.feedbackCard {
    margin: 1% 4%;
    background: transparent;
    border-color: var(--green);
}

.feedbackCard__header {
    padding: .50rem 1.25rem;
    margin-bottom: 0;
    background-color: rgba(0, 0, 0, 0.10);
    border-bottom: 1px solid rgba(0, 0, 0, .125);
}

.feedback__body {
    padding: 1rem;
}
:root {
  --dark: #20232F;
  --blue: #2365A8;
  --orange: #D56031;
  --white: #E7E0D7;
  --green: #32B777;
  --pink: #FA3D56;
  --violet: #D452FD;
}

* {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #20232F;
  background-color: var(--dark);
  color: #E7E0D7;
  color: var(--white);
}

button {
  height: 35px;
  width: 160px;
  margin: 2px 3px;
  border: none;
  outline: none;
  font-size: 16px;
  background-color: transparent;
  box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1),
    1px 1px 3px rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  cursor: pointer;
  width: 16%;
  min-width: 130px;

}

button:focus {
  outline: none;
}

.alert_c {
  background-color: #FA3D56;
  background-color: var(--pink);
  padding: 1%;
  margin: 3% 1%;
  border-radius: 5px;
}

@media (max-width:1160px) {
  .app {
    flex-direction: column;
  }

}
